import * as React from 'react';

export const BlackPearlShipIcon = () => {
  /* tslint:disable */
  const imagePath = "data:img/png;base64,";
  /* tslint:enable */

  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      width="101"
      height="104"
      viewBox="0 0 101 104"
    >
      <image
        x="0"
        y="0"
        width="101"
        height="104"
        xlinkHref={imagePath}
      />
    </svg>
  );
};


